@use 'base/variables' as v;


.home__footer {
    margin-top: calc(v.$separacion / 2);

    background-color: v.$blanco;
    margin-bottom: 0;

    .home__barra {
        padding: 5rem;
        display: flex;
        justify-content: flex-end;
        background-image: linear-gradient(to top, rgba(v.$grisAzul, 1), rgba(v.$naranjaOscuro, 0.3));
        box-shadow: inset 0px 3px 8px rgba(v.$negro, .5);
        box-shadow: inset 0px -3px 8px rgba(v.$negro, .5);



    }
    .copyright {
        color: v.$negro;
        text-align: center;
        margin-top: 1.5rem;
        padding-bottom: 1.5rem;
        
    }
}




